export default function Ocean({
  className,
  topColor = '#47d5fc',
  middleColor = '#1cbeeb',
  bottomColor = '#0782a6',
  animationDuration = 2000
}: OceanProps) {
  const [currentPath, setCurrentPath] = useState(0)

  const [{ d: top }, setTop] = useSpring(() => ({
    d: TOP_PATHS[0],
    config: { duration: animationDuration, easing: easings.easeInOutSine }
  }))

  const [{ d: middle }, setMiddle] = useSpring(() => ({
    d: MIDDLE_PATHS[0],
    config: { duration: animationDuration, easing: easings.easeInOutSine }
  }))

  const [{ d: bottom }, setBottom] = useSpring(() => ({
    d: BOTTOM_PATHS[0],
    config: { duration: animationDuration, easing: easings.easeInOutSine }
  }))

  useEffect(() => {
    const timeouts: NodeJS.Timeout[] = []

    timeouts.push(
      setTimeout(() => {
        const paths = range(TOP_PATHS.length)
        paths.splice(currentPath, 1)
        const newPath = sample(paths) as number
        setTop({ d: TOP_PATHS[newPath] })

        timeouts.push(
          setTimeout(() => {
            setMiddle({ d: MIDDLE_PATHS[newPath] })

            timeouts.push(
              setTimeout(() => {
                setBottom({ d: BOTTOM_PATHS[newPath] })
                setCurrentPath(newPath)
              }, animationDuration / 3)
            )
          }, animationDuration / 3)
        )
      }, animationDuration / 3)
    )

    return () => {
      timeouts.map(clearTimeout)
    }
  }, [currentPath])

  return (
    <svg viewBox="0 0 900 100" preserveAspectRatio="none" className={className}>
      <g transform="translate(0, -500)">
        <animated.path d={top} fill={topColor}></animated.path>
        <animated.path d={middle} fill={middleColor}></animated.path>
        <animated.path d={bottom} fill={bottomColor}></animated.path>
      </g>
    </svg>
  )
}
